body::before {
    content: '';
    height: 50vh;
    width: 100%;
    position: fixed;
    left: 0;
    background: #1c2020;
    z-index: 6;
    top: 0%;
    -webkit-transform: translateY(-100%);
    -ms-transform: translateY(-100%);
        transform: translateY(-100%);
}
body::after {
    content: '';
    height: 50vh;
    width: 100%;
    position: fixed;
    left: 0;
    background: #1c2020;
    z-index: 6;
    bottom: 0%;
    -webkit-transform: translateY(100%);
    -ms-transform: translateY(100%);
        transform: translateY(100%);
}
.home-web {
    width: 100%;
    z-index: 5;
}
.home-web-wrapper {
    background-image: -webkit-radial-gradient(51% 75%, circle, #0d2339, #000000);
    background-image: -o-radial-gradient(51% 75%, circle, #0d2339, #000000);
    background-image: radial-gradient(circle at 51% 75%, #0d2339, #000000);
    width: 100%;
    height: 100%;
    min-height: 500px;
    min-width: 550px;
    position: relative;
}
.home-wrapper {
    width: 100%;
    height: 100%;
}
.home-mobile {
    overflow-y: hidden;
    width: 100%;
    height: 100%;
}
.home-web {
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    position: relative;
    top: 50%;
    -webkit-transform: translate(0 ,-50%);
        -ms-transform: translate(0 ,-50%);
            transform: translate(0 ,-50%);
}
.err-msg {
    color: white;
    font-size: 17px;
    position: fixed;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%, -50%);
        -ms-transform: translate(-50%, -50%);
            transform: translate(-50%, -50%);
    z-index: 100;
    direction: rtl;
    font-family: Heebo, Alef-Regular, Alef-Bold, sans-serif;
}
.home-start-button-wrapper {
    height: 55px;
    width: 230px;
    margin: auto;
    position: relative;
}
.start-button {
    background: -webkit-gradient(linear, left top, left bottom, from(#1a182a), to(#1a182a)), -webkit-gradient(linear, left top, right top, from(#02c0fd), color-stop(53%, #fecf03), to(#fd504f));
    background: -webkit-linear-gradient(#1a182a, #1a182a), -webkit-linear-gradient(left, #02c0fd, #fecf03 53%, #fd504f);
    background: -o-linear-gradient(#1a182a, #1a182a), -o-linear-gradient(left, #02c0fd, #fecf03 53%, #fd504f);
    background: linear-gradient(#1a182a, #1a182a), linear-gradient(to right, #02c0fd, #fecf03 53%, #fd504f);
}
.button-shadow {
    width: calc(100% - 20px);
    height: 50%;
    position: relative;
    top: 40%;
    margin: auto;
    border-radius: 19.4px;
    -webkit-filter: blur(5px);
    filter: blur(5px);
    border: solid 2.2px transparent;
    background-image: -webkit-linear-gradient(358deg, #c6e6ff, #94c8ff), -webkit-linear-gradient(left, #02c0fd, #fecf03 53%, #fd504f);
    background-image: -o-linear-gradient(358deg, #c6e6ff, #94c8ff), -o-linear-gradient(left, #02c0fd, #fecf03 53%, #fd504f);
    background-image: linear-gradient(92deg, #c6e6ff, #94c8ff), linear-gradient(to right, #02c0fd, #fecf03 53%, #fd504f);
    background-origin: border-box;
    background-clip: content-box, border-box;
}
@-webkit-keyframes come-from-bottom {
    from {
        bottom: -500px;
    }
    to {
        bottom:0px;
        opacity: 1;
    }
}
@keyframes come-from-bottom {
    from {
        bottom: -500px;
    }
    to {
        bottom:0px;
        opacity: 1;
    }
}
.img-man-second, .img-man-third {
    position: relative;
    opacity: 0;
    -webkit-animation: come-from-bottom 1.5s ease-out 1s;
            animation: come-from-bottom 1.5s ease-out 1s;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}
.img-man-second {
    margin-left:30px;
    margin-right:150px;
}
.img-man-third {
    margin-left:150px;
    margin-right:30px;
}
@media (max-width: 900px) {
    .img-man-second {
        margin-left:30px;
        margin-right:30px;
    }
    .img-man-third {
        margin-left:30px;
        margin-right:30px;
    }
}
.img-man-first {
    position: relative;
    opacity: 0;
    -webkit-animation: come-from-bottom 1.5s ease-out;
            animation: come-from-bottom 1.5s ease-out;
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}
.images-wrapper {
    position: absolute;
    bottom:0;
    left: 50%;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
    width: 100%;
    overflow: hidden;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: baseline;
        align-items: baseline;
}


